<template>
  <div id="blackboardWriting">
    <head-top title="板书"></head-top>
    <van-pull-refresh v-model="isLoading" @refresh="getDataList" class="pull_fresh">
      <div class="main" v-if="dataList.length>0">
        <div v-for="(item, index) in dataList" :key="index" class="item">
          <div class="bg fL">
            <r-image :ref="'refImg'+index" width="80px" height="58px" :src="item.img"></r-image>
          </div>
          <div class="time fR">
            <div class="info">{{item['time'] | formatDate(item['time']) }}</div>
          </div>
        </div>
      </div>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import RImage from "@/components/RImage";
import { ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: { headTop, RImage },
  props: {},
  data() {
    return {
      isLoading: false,
      dataList: []
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.isLoading = true;
      this.dataList = [];
      this.$gatewayHttp.boardNote
        .getList({
          courseID: this.$store.getters.courseID
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              let sub = {
                img: this.$gatewayHttp.resourse.picture(
                  item.BNContent,
                  ModulePicture.BoardNote
                ),
                state: item.State,
                title: item.BNTitle,
                time: item.CreateTime,
                id: item.BNID,
                content: item.BNContent
              };

              let index = this.dataList.findIndex(
                k => JSON.stringify(sub) === JSON.stringify(k)
              );
              // 避免重复数据
              if (index === -1) this.dataList.push(sub);
            });

            this.autoShow();
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    autoShow() {
      if (this.$store.getters.boardNote && this.$store.getters.boardNote.id) {
        let obj = this.dataList.find(
          r => r.id === this.$store.getters.boardNote.id
        );

        if (obj) {
          setTimeout(() => {
            let imgDom = this.$refs["refImg0"][0];
            imgDom.preview();
            this.$store.commit("SET_BOARDNOTE", {});
          }, 500);
        }
      }
    }
  },
  watch: {
    "$store.getters.boardNote"(newVal, oldVal) {
      if (newVal && newVal.id) {
        this.getDataList();
      }
    }
  }
};
</script>

<style lang='less' scoped>
#blackboardWriting {
  .main {
    width: 100vw;
    // height: 375px;
    max-height: calc(100vh - 4.5rem);
    overflow-y: auto;
    // padding-bottom: 2vh;
    background: #fff;
    .item {
      height: 89px;
      width: 92vw;
      border-bottom: 1px solid #dddddd;
      margin: 0 auto;
      text-align: left;
      overflow: hidden;
    }
    .bg {
      width: 80px;
      height: 58px;
      margin: 15px;
      margin-left: 0px;
      text-align: center;
    }
    .time {
      margin-top: 15px;
      font-size: 1.2rem;
      color: rgba(102, 102, 102, 1);
      width: 70%;
    }
  }

  .pull_fresh {
    /deep/ .van-pull-refresh__track {
      height: calc(100vh - 4.5rem);
    }
  }
}
</style>


